 {\n      top: -4px;\n      left: -50px;\n      width: 50px;\n      height: 50px;\n    }\n\n    &.-right {\n      border-radius:  10px 0 10px 10px;\n      margin-right: 50px;\n\n      .corner-png-right {\n        display: block;\n      }\n\n      .bubble-corner {\n        right: -50px;\n      }\n    }\n  }\n\n  // types\n  &.type-secondary {\n    background-color: $color-grey-light;\n\n    .corner-png {\n      display: none;\n    }\n\n    .bubble-corner {\n      //display: block;\n      fill: $color-grey-light;\n      stroke: $color-grey-light;\n    }\n  }\n\n  &.type-primary {\n    background-color: #ffffff;\n    color: $color-text;\n\n    .bubble-corner {\n      fill: #ffffff;\n      stroke: $color-grey-light;\n    }\n\n    &:after {\n      content: '';\n      display: block;\n      position: absolute;\n      width: 90%;\n      left: 5%;\n      height: 60px;\n      bottom: 7px;\n      box-shadow: 0 15px 60px 0 rgba(0, 31, 82, 0.14);\n      z-index: -1;\n    }\n  }\n}\n\n.bubble.type-link.size-sm {\n  text-decoration: none;\n  color: $color-text;\n  font-size: 16px;\n  line-height: 22px;\n  padding: 15px 20px;\n  border-radius: 0 4px 4px 4px;\n  width: 20%;\n  margin-left: 25px;\n  border: 2px solid $color-grey-light;\n  background-color: $color-grey-light;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  -webkit-transition: $hoverAnimation;\n  -moz-transition: $hoverAnimation;\n  -o-transition: $hoverAnimation;\n  transition: $hoverAnimation;\n  &:after {\n    -webkit-transition: $hoverAnimation;\n    -moz-transition: $hoverAnimation;\n    -o-transition: $hoverAnimation;\n    transition: $hoverAnimation;\n    box-shadow: 0;\n    content: '';\n    display: block;\n    position: absolute;\n    width: 90%;\n    left: 5%;\n    height: 60px;\n    bottom: 7px;\n    z-index: -1;\n  }\n\n  //z-index: 1;\n  //position: relative;\n  .corner-png-right {\n    display: none;\n  }\n\n  &:hover, &:focus {\n    background-color: $color-milky;\n    border-color: $color-milky;\n    .bubble-corner {\n      display: block;\n      fill: $color-milky;\n      stroke: $color-milky;\n      -webkit-transition: $hoverAnimation;\n      -moz-transition: $hoverAnimation;\n      -o-transition: $hoverAnimation;\n      transition: $hoverAnimation;\n    }\n    &:after {\n      -webkit-transition: $hoverAnimation;\n      -moz-transition: $hoverAnimation;\n      -o-transition: $hoverAnimation;\n      transition: $hoverAnimation;\n      content: '';\n      display: block;\n      position: absolute;\n      width: 90%;\n      left: 5%;\n      height: 60px;\n      bottom: 7px;\n      box-shadow: 0 15px 60px 0 rgba(0, 31, 82, 0.14);\n      z-index: -1;\n    }\n\n  }\n\n  &:active {\n    background-color: $color-grey;\n    border-color: $color-grey;\n\n    .bubble-corner {\n      fill: $color-grey;\n      stroke: $color-grey;\n    }\n  }\n\n  .bubble-corner {\n    display: block;\n    top: -2px;\n    left: -25px;\n    width: 25px;\n    height: 25px;\n    fill: $color-grey-light;\n    stroke: $color-grey-light;\n    -webkit-transition: $hoverAnimation;\n    -moz-transition: $hoverAnimation;\n    -o-transition: $hoverAnimation;\n    transition: $hoverAnimation;\n  }\n\n  &.-right {\n    border-radius: 4px 0 4px 4px;\n    margin-right: 25px;\n    margin-left: 0;\n\n    .bubble-corner {\n      right: -25px;\n      left: auto;\n    }\n  }\n\n}\n\n.bubble.full-width {\n  width: 100% !important;\n}\n\n.bubble.auto-width {\n  width: auto !important;\n}\n\n\n.bubble .bubble-corner {\n  position: absolute;\n  z-index: 1;\n}\n\n.bubble.-right {\n  margin-left: 0;\n\n  .corner-png-right {\n    display: block;\n  }\n  .bubble-corner {\n    transform: scale(-1, 1);\n    left: auto;\n  }\n}\n\n\n.bubble-inner {\n  display: inline-block;\n  height: 100%;\n  opacity: 1;\n  transition: opacity $translate-animation ease-in, height $translate-animation ease-in;\n\n  &.isHide {\n    transition: opacity $translate-animation ease-in, height $translate-animation ease-in;\n    opacity: 0.01;\n  }\n\n  &.isShow {\n    transition: opacity $translate-animation ease-in, height $translate-animation ease-in;\n    opacity: 1;\n  }\n}\n\n\n\n.bubble.w_45 {\n  width: 450px;\n}\n\n.bubble.w_35p {\n  width: 35%;\n}\n\n.bubble.w_60p {\n  width: 60%;\n}\n\n.bubble.w_70p {\n  width: 70%;\n}\n\n\n@media (max-width: $screen-xs-max) {\n\n  .bubble .emoji-img {\n    width: 20px;\n    height: 20px;\n    margin-bottom: 2px;\n  }\n\n  .bubble.size-lg {\n    font-size: 16px;\n    line-height: 26px;\n    padding: 20px 20px 20px 20px;\n    border-width: 2px;\n    margin-left: 16px;\n\n    .corner-png {\n      top: -2px;\n      left: -16px;\n      width: 16px;\n      height: 22px;\n    }\n\n    .corner-png-right {\n        top: -2px;\n        right: -16px;\n        width: 16px;\n        height: 22px;\n    }\n\n    .bubble-corner {\n       top: -2px;\n       left: -25px;\n       width: 25px;\n       height: 25px;\n    }\n  }\n\n  .bubble.size-md {\n    font-size: 16px;\n    line-height: 26px;\n    padding: 15px 20px;\n    border-width: 2px;\n    margin-left: 25px;\n\n    &.-right {\n      margin-right: 25px;\n      .bubble-corner {\n        right: -24.9px;\n        left: inherit;\n      }\n    }\n\n    .corner-png-right {\n      top: -2px;\n      right: -16px;\n      width: 16px;\n      height: 22px;\n    }\n\n    .corner-png {\n      top: -2px;\n      left: -16px;\n      width: 16px;\n      height: 22px;\n    }\n\n    .bubble-corner {\n      top: -2px;\n      width: 25px;\n      height: 25px;\n      left: -24.9px;\n    }\n  }\n\n  .bubble.w_45 {\n    width: calc(90% - 25px);\n  }\n\n  .bubble.w_35p {\n    width: 75%;\n  }\n\n  .bubble.w_60p {\n    width: 85%;\n  }\n\n  .bubble.w_70p {\n    width: 85%;\n  }\n\n  .bubble.type-link.size-sm {\n    padding: 14px 20px;\n    border-radius: 27px;\n\n    &:hover:after {\n      display: none;\n    }\n\n    &:hover {\n      .bubble-corner {\n        display: none;\n      }\n    }\n\n    &.-right {\n      border-radius: 27px;\n    }\n\n    .corner-png-right {\n      display: none;\n    }\n\n    .corner-png-right {\n      display: none;\n    }\n\n    .bubble-corner {\n      display: none;\n    }\n\n    .bubble-inner {\n      br {  display: none;}\n    }\n  }\n\n //.bubble br{\n //  display: none;\n // }\n}\n\n\n@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {\n  .bubble.w_35p {\n    width: 60%;\n  }\n\n  .bubble.w_60p {\n    width: 60%;\n  }\n\n  .bubble.w_70p {\n    width: 80%;\n  }\n}\n\n.bubble.size-lg.with-video video {\n  width: 480px;\n  height: 480px;\n  border-radius: 8px;\n}\n\n@media (max-width: $screen-xs-max ){\n  //.bubble.type-link.size-sm .bubble-inner br, .bubble br {\n  //  display: none;\n  //}\n\n  .br-desctop br{\n    display: none;\n  }\n\n  .bubble.size-lg.with-video {\n    width: 100%;\n    height: auto;\n  }\n\n  .bubble.size-lg.with-video video {\n    width: 68vw;\n    height: 68vw;\n  }\n\n}\n\n@media (max-width: $screen-xs-min ){\n  .bubble.type-link.size-sm {\n    padding: 10px 20px;\n  }\n}\n","$color-blue: #369dff;\n$color-dark-blue: #3C5999;\n$color-grey: #d4d8db;\n$color-grey-light: #ebedef;\n$color-milky: #f4f6f8;\n$color-text: rgba(0, 0, 0, 0.8);"],"sourceRoot":""}]);

